<template>
  <div class="book" :style="{backgroundColor:bodyBg}">
    <van-nav-bar
      class="top"
      :style="isMask ? 'top:-80px' : 'top:0'"
      :title="bookData.title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    ></van-nav-bar>
    <div :style="{fontSize:Aa+'px'}" @click="mask">
      <div v-if="bookData.isVip != true">
        <p v-for="(item,index) in bookData.data" :key="index">{{item}}</p>
      </div>
      <div v-else>
        <p>{{bookData.title}}</p>
        <p class="pp">此章节需要开通vip才能阅读</p>
      </div>
    </div>
    <div class="bottom" :style="isMask ? 'bottom:-300px' : 'bottom:0'">
      <div class="fontSize clearfix">
        <van-button class="fl last" type="default" @click="minSize">Aa-</van-button>
        <div class="Aa">
          <van-slider
            v-model="Aa"
            bar-height="4px"
            :min="min"
            :max="max"
            button-size="14px"
            active-color="#ed424b"
          />
        </div>
        <van-button class="fr next" type="default" @click="maxSize">Aa+</van-button>
      </div>
      <div class="bg">
        <div class="bgItem" v-for="(item,index) in bgColor" :key="index" @click="tabBgColor(index)">
          <div class="bgColor" :style="{backgroundColor:item.bg}">
            <van-icon
              v-show="index == bgActive"
              name="success"
              color="#ed424b"
              size="20"
              class="icon"
            />
          </div>
          <div class="bgName">{{item.name}}</div>
        </div>
      </div>
      <div class="catalog">
        <van-button class="last" size="small" type="default" @click="last">上一章</van-button>
        <div class="list">
          <div @click="list">目录</div>
          <div class="bookMask" :style="isShow ? '0' : 'left:-500px'" @click="bookMask"></div>
          <div class="booklist" :style="isShow ? '0' : 'left:-500px'">
            <div class="listTitle">目录</div>
            <van-list
              v-model="loadOptions.isLoading"
              :finished="loadOptions.isFinished"
              :finished-text="loadOptions.finishedText"
              @load="onLoad"
              :offset="50"
            >
              <van-cell
                class="total"
                style="color:#333730;font-weight:bold;"
                :title="'共'+length+'章'"
              ></van-cell>
              <van-cell
                v-for="(item,index) in listData"
                :key="index"
                :title="item.title"
                @click="listClick(index)"
                :class="index == isIndex ? 'active' : ''"
                is-link
              />
            </van-list>
          </div>
        </div>
        <van-button class="next" size="small" type="default" @click="next">下一章</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { createNamespacedHelpers, mapGetters } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("bookModule");
export default {
  data() {
    return {
      Aa: 20,
      min: 10,
      max: 30,
      bookId: {},
      link: "",
      bookData: [],
      isMask: true,
      isIndex: 0,
      length: 0,
      bgColor: [
        { bg: "rgb(232,226,211)", name: "羊皮纸" },
        { bg: "rgb(248,247,243)", name: "淡雅白" },
        { bg: "rgb(196,222,237)", name: "冰爽蓝" },
        { bg: "rgb(224,182,196)", name: "浪漫粉" },
        { bg: "rgb(166,188,156)", name: "护眼绿" }
      ],
      bgActive: 0,
      bodyBg: "rgb(232,226,211)",
      isShow: false,
      listData: [],
      allListData: [],
      loadOptions: {
        //是否处于加载状态，加载过程中不触发load事件
        isLoading: true,
        //是否完成加载所有数据
        isFinished: false,
        finishedText: "没有章节可加载了"
      },
      //每次加载20条数据
      loadCount: 20,
      isSort: false
    };
  },
  created() {
    this.bookId = this.$route.query;
    if (Object.keys(this.bookId).length == 1) {
      if (this.bookIndex.length > 0) {
        this.bookIndexData();
      }
      this.getbookChapters();
    } else {
      this.isIndex = this.bookId.index - 0;
      this.isSort = this.bookId.isSort;
      this.getbookChapters();
    }
  },
  computed: {
    ...mapState(["bookIndex"])
  },
  methods: {
    ...mapMutations(["changeData"]),
    bookIndexData() {
      for (let i = 0; i < this.bookIndex.length; i++) {
        if (this.bookId.bookId == this.bookIndex[i].id) {
          this.isIndex = this.bookIndex[0].index;
        }
      }
    },
    tabBgColor(index) {
      this.bgActive = index;
      this.bodyBg = this.bgColor[index].bg;
    },
    //返回
    onClickLeft() {
      let bookCache = [];
      bookCache = { index: this.isIndex, id: this.bookId.bookId };
      this.changeData({
        //**********key要和传回来的值一致*****//
        key: "bookIndex",
        value: bookCache
      });
      this.$router.go(-1);
    },
    mask() {
      this.isMask = !this.isMask;
    },
    list() {
      this.isMask = true;
      this.isShow = true;
    },
    bookMask() {
      this.isShow = false;
    },
    getbookChapters() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-chapters/" + this.bookId.bookId
      })
        .then(result => {
          this.$toast.clear();
          console.log(result.data);
          if (Object.keys(result.data).length > 0) {
            this.length = result.data.chapters.length;
            if (this.isSort) {
              this.allListData = result.data.chapters.sort(this.sortNumber);
            } else {
              this.allListData = result.data.chapters;
            }
            this.listData = this.allListData.splice(0, this.loadCount);
            if (this.allListData.length > 0) {
              this.loadOptions.isLoading = false;
            } else {
              this.loadOptions.isLoading = true;
              this.loadOptions.isFinished = true;
            }
            if (this.listData.length == 0) {
              this.loadOptions.finishedText = "";
            }
            let link = "";
            link = this.listData[this.isIndex].link;
            this.getBook(link);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    getBook(link) {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url:
          "https://api.kele8.cn/agent/http://novel.kele8.cn/chapters/" +
          encodeURIComponent(link)
      })
        .then(result => {
          this.$toast.clear();
          // console.log(result.data);
          if (Object.keys(result.data).length > 0) {
            let data = [];
            data = result.data.chapter.cpContent.split("\n");
            for (let i = 0; i < data.length; i++) {
              data[i] = data[i].toString().replace(/\s*/g, "");
            }
            data.unshift(result.data.chapter.title);
            result.data.chapter.data = data;
            this.bookData = result.data.chapter;

            this.isShow = false;
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    sortNumber(a, b) {
      if (this.isSort) {
        return b.order - a.order;
      } else {
        return a.order - b.order;
      }
    },
    last() {
      document.documentElement.scrollTop = 0;
      if (this.isIndex <= 0) {
        this.isIndex = 0;
      } else {
        this.isIndex--;
        this.getbookChapters();
      }
    },
    next() {
      document.documentElement.scrollTop = 0;
      if (this.isIndex >= this.length) {
        this.isIndex = this.length;
        this.getbookChapters();
      } else {
        this.isIndex++;
        this.getbookChapters();
      }
    },
    minSize() {
      if (this.Aa <= this.min) {
        this.Aa = this.min;
      } else {
        this.Aa -= 2;
      }
    },
    maxSize() {
      if (this.Aa >= this.max) {
        this.Aa = this.max;
      } else {
        this.Aa += 2;
      }
    },
    listClick(index) {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-chapters/" + this.bookId.bookId
      })
        .then(result => {
          this.$toast.clear();
          // console.log(result.data);
          if (Object.keys(result.data).length > 0) {
            document.documentElement.scrollTop = 0;
            this.isIndex = index;
            this.getbookChapters();
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    onLoad() {
      setTimeout(() => {
        this.listData.push(...this.allListData.splice(0, this.loadCount));
        // 数据全部加载完成
        this.loadOptions.isLoading = false;
        if (this.allListData.length == 0) {
          this.loadOptions.isFinished = true;
        }
      }, 200);
    }
  }
};
</script>

